<template>
<div class="user">
    <div class="pan1">
        <div class="header">
            <router-link :to="{path:'/'}">主页/</router-link>      
        </div>
        <div class="inner">
                <div>
                    <img :src="user.avatar_url"></img>
                    <span>{{user.loginname}}</span>
                </div>
                <div>
                    <span>{{user.score}}积分</span>
                </div>
                <div>
                    <a>https://github.com/{{user.githubUsername}}</a>
                </div>
                <div>
                    <span>{{user.create_at}}&nbsp;&nbsp;注册</span>
                </div>
            </div>
    </div>
    <div class="pan1">
        <div class="header">
            <span>最近创建的话题</span>            
        </div>
        <div class="cell" v-for="item in user.recent_topics">  
            <router-link :to="{path:'#'}"><img :src="item.author.avatar_url"></router-link>    
            <router-link :to="{name:'topic',params:{id:item.id,name:item.author.loginname}}">{{item.title}}</router-link>
      </div>
    </div>
    <div class="pan1">
        <div class="header">
            <span>最近参与的话题</span>
        </div>
        <div class="cell" v-for="item in user.recent_replies">  
            <router-link :to="{path:'#'}"><img :src="item.author.avatar_url"></router-link>    
            <router-link :to="{name:'topic',params:{id:item.id,name:item.author.loginname}}">{{item.title}}</router-link>
      </div>
    </div>
</div>
</template>
<script>
export default {
  name: "component_name",
  data () {
    return {
        user:{
    recent_replies:[]
        }
    };
  },
  methods:{
   
  },
  mounted(){
    this.$http({
      url:'https://cnodejs.org/api/v1/user/'+this.$route.params.name,
      method:'get'
    })
    .then((res)=>{
      this.user=res.data.data 
      console.log(this.user.recent_topics)
    })
    .catch((error)=>{

    })
  }
}
</script>
<style scoped>
.user{
  width: 88%;
  margin: 0 auto;
  margin-top: 15px;
  
}
.pan1{
    margin-bottom: 10px;
}
.header{
     background: #f6f6f6;
     padding: 10px;
}
.inner{
    background: #fff;
    padding: 10px;
    border-top: 1px solid #e5e5e5;
}
.inner div>img{
    width: 40px;
    height: 40px;
    margin-right: 10px;
}
.inner>div{
    margin-bottom: 10px;
}
.cell{
    padding: 10px;
    background: #fff;
    border-top: 1px solid #e5e5e5;
}
.cell a>img{
width: 30px;
height: 30px;
}
</style>


