<template>
<div v-if="isShow" class="dialog">
    <div class="dialog-cover" @click="close"></div>
    <div class="dialog-content">
        <p class="dialog-close" @click="close">x</p>
        <slot>sss</slot>
    </div>
</div>
</template>
<script>
export default {
  name: "component_name",
  props:{
      isShow:{
          type:Boolean,
          default:false
      }
  },
  data () {
    return {
        
    };
  },
  methods:{
      close(){
          this.$emit('on-close')
      }
  }
}
</script>
<style scoped>
.dialog{
    position: fixed;
    width: 100%;
    height: 100%;
}
.dialog-cover{
    position: fixed;
    width: 100%;
    height: 100%;
    background: #000;
    opacity: 0.5;
    z-index: 5;
    top:0;
    left: 0;
}
.dialog-content{
    width: 50%;
    max-width: 50%;
    background: #fff;
    position: fixed;
    top: 20%;
    left: 50%;
    margin-left: -25%;
    z-index: 10;
    padding: 2%;
    border: 2px solid #464068;
    line-height: 1.6;
}
.dialog-close{
    width: 20px;
    height: 20px;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
    text-align: center;
}
.dialog-close:hover{
    background: blue;
}
</style>


