<template>
<div class="head">
    <div class="head-inner clearfix">
        <div class="head-inner-left">
        <router-link class="brand" :to="{path:'/'}"><img src="../assets/cnodejs.svg"/></router-link>
        <form>
            <input type="text">
        </form>
        </div>
        <div class="head-inner-right">
         <ul>
             <router-link :to="{path:'/'}">
               <li>首页</li>
             </router-link>
            <li>新手入门</li>
            <li>api</li>
            <li @click="AboutClick">关于</li>
            <li>注册</li>
            <li>登陆</li>
        </ul>
        </div>
    </div>
    <dia-log :isShow="isShowAbout" @on-close="close('isShowAbout')"></dia-log>
</div>
</template>

<script>
import DiaLog from './dialog'
export default {
  name: "component_name",
  data () {
    return {
        isShowAbout:false,
    };
  },
  methods:{
      AboutClick(){
          this.isShowAbout=true
      },
      close(attr){
          this[attr]=false
      }
  },
  components:{
      DiaLog,
  }
}
</script>
<style scoped>
.head{
    width: 100%;
    background: #444;
    font-size: 13px;
}
.head-inner{
    width: 90%;
    margin: 0 auto;
    padding: 5px;
}
.head-inner-left{
    float: left;
}
.head-inner-right{
    float: right;
}
/* //清除浮动 */
.clearfix::after{
    content:"";/*设置内容为空*/
    height:0;/*高度为0*/
    line-height:0;/*行高为0*/
    display:block;/*将文本转为块级元素*/
    visibility:hidden;/*将元素隐藏*/
    clear:both;/*清除浮动*/
}
.clearfix{
    zoom:1;/*为了兼容IE*/
}
.head-inner .brand{
    display: block;
    width: 120px;
    height: 34px;
    line-height: 34px;
    padding: 3px 20px;
    float: left;
}
.head-inner form{
    float: left;
    margin-top: 10px;
}
.head-inner-right ul li{
    float: left;
     padding: 10px 15px;
     color: #ccc;
     cursor: pointer;
}
.head-inner-right ul li:hover{
    color: #fff;
}


    
</style>


