import Vue from 'vue'
import Router from 'vue-router'
import Index from '../components/index'
import Topic from '../components/topic'
import User from '../components/user'
Vue.use(Router)
export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      name:'index',
      component: Index
    },
    {
      path:'/topic:id',
      name:'topic',
      component:Topic
    },
    {
      path:'/user:name',
      name:'user',
      component:User
    }
  ]
})



