<template>
<div class="index">
  <div class="index-head">
     <!-- <router-link :to="{path:'/',query:{tab:'all'}}" active-class="active">全部</router-link>
     <router-link :to="{path:'/',query:{tab:'good'}}" active-class="active">精华</router-link>
     <router-link :to="{path:'/',query:{tab:'share'}}">分享</router-link>
     <router-link :to="{path:'/',query:{tab:'ask'}}">问答</router-link>
     <router-link :to="{path:'/',query:{tab:'job'}}">招聘</router-link>
     <router-link :to="{path:'/',query:{tab:'dev'}}">客户端测试</router-link> -->
     <router-link v-for="tabitem in tablist" :to="{path:'/',query:{tab:tabitem.tab}}" tag="a" active-class="active">{{tabitem.name}}</router-link>
  </div>
  <div class="index-inner">
    <div class="index-topiclist">
      <div class="cell" v-for="item in topicdata">
        <!-- <a class="cell-left" href=""><img :src="item.author.avatar_url"></a> -->
        <router-link class="cell-left" :to="{name:'user',params:{name:item.author.loginname}}"><img :src="item.author.avatar_url"></router-link>
        <div class="spanlist cell-left">
          <span class="spanlist1">{{item.reply_count}}</span>
          <span>/</span>
          <span class="spanlist2">{{item.visit_count}}</span>
        </div>
            <a class="cell-right" href="">
          <img src="../assets/cnodejs.svg">
          <span>eee</span>
        </a>
        <div class="topic-title">
          <span :class="[{'topic-titlspan1':item.top!=false},{'topic-titlspan2':item.top!=true}]">{{func(item)}}</span>
          <!-- <a href="">{{item.title}}</a> -->
          <router-link :to="{name:'topic',params:{id:item.id,name:item.author.loginname}}">{{item.title}}</router-link>
        </div>
      </div>
    </div>
  </div>
</div>
</template>
<script>
export default {
  name: "component_name",
  data () {
    return {
      limit:20,
      topicdata:[],
      tablist:[
        {
        tab:'all',
        name:"全部"
        },
        {
          tab:'good',
          name:"精华"
        },
          {
          tab:'share',
          name:"分享"
        },
          {
          tab:'ask',
          name:"问答"
        },
          {
          tab:'job',
          name:"招聘"
        },
            {
          tab:'dev',
          name:"客户端测试"
        }
      ]
    };
  },
  methods:{
    //下拉加载
    scrollMethod(){
            var scrollTop=document.documentElement.scrollTop;
            var clientHeight =document.documentElement.clientHeight;
            var scrollHeigh=document.documentElement.scrollHeight;
            console.log(scrollTop,clientHeight,scrollHeigh)
            if(scrollTop+clientHeight+1>=scrollHeigh){
              this.limit+=20;
               this.getData()
            } 
    },
    getData(){
      this.$http({
        url:'https://cnodejs.org/api/v1/topics',
        method:'get',
        params:{
          page:1,
          limit:this.limit,
          tab:this.$route.query.tab 
        }
      })
      .then((res)=>{
        this.topicdata=res.data.data
        
      })
      .catch((error)=>{
        console.log(error)

      })
    },
    func(item){
      if(item.top!=false){
        return "置顶"
      }
      if(item.good!=false){
        return "精华"
      }
      if(item.tab=="ask"){
        return "问答"
      }
      if(item.tab=="share"){
        return "分享"
      }
      if(item.tab=="job"){
        return "招聘"
      }
      if(item.tab=="dev"){
        return "客户端测试"
      }

    }
 
 
  },
  watch: {    
    '$route' (to, from) {   
        //  this.$router.go(0);  
        this.limit=20;
        this.getData(); 
 }},
  mounted(){
    this.getData();
    window.addEventListener('scroll',this.scrollMethod)
    console.log(this.$route)
       
  }
}
</script>
<style scoped>
.index{
  width: 88%;
  margin: 0 auto;
  margin-top: 15px;
}
.index-head{
  padding: 10px;
  background: #f6f6f6;
  font-size: 13px;
}
.index-head>a.router-link-exact-active{
  background: #80bd01;
  color: #fff;
  padding: 3px 4px;
}
.index-head>a{
  margin: 0 10px;
  color: #80bd01;
}

.index-head>a:hover{
  color: blue;
} 
.index-inner{
  background: #fff;
}
.index-topiclist{
  line-height: 2em;

}

.index-topiclist img{
  width: 30px;
  height: 30px;
}
.index-topiclist .cell{
  border-bottom:1px solid #f0f0f0;
  padding: 10px 0 10px 10px;
 overflow: hidden;
}
.cell-left{
  float: left;
}
.cell-right{
  float: right;;
  font-size: 11px;
  display: inline-block;
}
.cell::after{
    content:"";/*设置内容为空*/
    height:0;/*高度为0*/
    line-height:0;/*行高为0*/
    display:block;/*将文本转为块级元素*/
    visibility:hidden;/*将元素隐藏*/
    clear:both;/*清除浮动*/
}
.cell-right>span{
  display: inline-block;
  min-width: 50px;
  text-align: center;
}
.cell-right>img{
  width: 18px;
  height: 18px;
  margin-right: 7px;
}
.spanlist{
  width: 70px;
  display: inline-block;
  text-align: center;
  margin-left: 10px;
}
.spanlist .spanlist1{
  color: #9e78c0;
}
.spanlist .spanlist2{
  font-size: 10px;
  color: #b4b4b4;
}
.topic-titlspan1{
  background: #80bd01;
  color: #Fff;
  padding: 2px 4px;
  font-size: 12px;
}
.topic-titlspan2{
  background: #e5e5e5;
  color: #999;
  padding: 2px 4px;
  font-size: 12px;
}

.topic-title a{
  color: #333;
  display: inline-block;
  font-size: 16px;
}
topic-title a:visited{
  color:#888;
}
</style>


