<template>
<div class="topic">
  <div class="sidebar">
    <div class="heade">作者</div>
    <div class="inner">
      <router-link class="user-avatar" :to="{name:'user',params:{name:user.loginname}}"><img :src="user.avatar_url"></router-link>
      <span class="user-name">{{user.loginname}}</span>
      <span class="user-span">积分:{{user.score}}</span>
      <span class="user-span">https://github.com/{{user.githubUsername}}</span>
    </div>
  </div>
  <div class="content">
  <div class="topic-panel">
    <div class="topic-panel-head">
      <span class="span1">{{article.title}}</span>
    </div>
    <div class="topic-panel-inner">
      <div class="topic-panl-innercont">
        <div v-html="article.content"></div>
      </div>
    </div>
  </div>
  <div class="topic-panel">
    <div class="topic-panel-head">
      <span>回复</span>
    </div>
    <div class="cell" v-for="item in article.replies">
      <router-link :to="{path:'/'}"><img :src="item.author.avatar_url"></router-link>
      <div class="cell-inner">
        <span>{{item.author.loginname}}</span>
        <span>{{dealCommentTime(item.create_at)}}</span>
        <span v-if="item.ups.length>0" class="spanicon">
         <img class="icon" src="../assets/thumbs-up.svg">
          <span>{{item.ups.length}}</span>
        </span>
        <p v-html="item.content"></p>
      </div>
    </div>
    </div>
  </div>
</div>
</template>
<script>
export default {
  name: "component_name",
  data () {
    return {
     article:{
        
     },
     user:{}
    };
  },
  methods:{
      dealCommentTime(time) {
            return String(time).match(/.{16}/)[0].replace(/.{2}/, '').replace(/[T]/, ' ');
        },
  },
  mounted(){
    this.$http({
      url:'https://cnodejs.org/api/v1/topic/'+this.$route.params.id,
      method:'get'
    })
    .then((res)=>{
      this.article=res.data.data;  
    })
    .catch((error)=>{

    })
    this.$http({
      url:'https://cnodejs.org/api/v1/user/'+this.$route.params.name,
      method:'get'
    })
    .then((res)=>{
      console.log(res.data.data)
      this.user=res.data.data 
      console.log(user)
    })
    .catch((error)=>{

    })
  }
}
</script>
<style scoped>
  img{max-width: 100%;}
.topic{
  width: 88%;
  margin: 0 auto;
  margin-top: 15px;
}
.content{
 margin-right: 450px;
}
.topic-panel{
  margin-bottom: 13px;
  background: #fff;
}
.topic-panel-head{
  padding: 10px;
}
.topic-panel-head .span1{
  font-size: 22px;
  font-weight: bold;
  display: inline-block;
  margin: 4px 0;
  width: 70%;
}
.topic-panel-inner{
  border-top: 1px solid #e5e5e5;
  padding: 10px;
}
.topic-panl-innercont{
  margin: 0 10px;
  overflow: hidden;
}
.cell{
  padding: 10px;
  border-top: 1px solid #e5e5e5;
  overflow: hidden;
}
.cell a{
  display: inline-block;
  float: left;
    width: 30px;
  height: 30px;;
}
.cell-inner{
margin-left: 50px;
}
.spanicon{
  float: right;
  display: inline-block;
  font-size: 15px;
  margin-right: 10px;
  color: gray;
}
.icon{
  width: 14px;
  height: 16px;
  display: inline-block;
  margin-top: -5px;
}
.sidebar{
  width: 32%;
  background: #fff;
  float: right;
   
}
.sidebar .heade{
  background: #f6f6f6;
   padding: 10px;
}
.sidebar .inner{
  padding: 10px;
}
.user-span{
  display: block;
  color: #333;
  margin-top: 10px;
}
.user-name{
  color: #778087;
}
.user-avatar{
    margin-right: 10px;
}
.user-avatar>img{
  vertical-align: middle;
  width: 48px;
  height: 48px;
}
</style>


